.container {
  background-color: #ff4136;
  width: 65%;
  height: 80%;
  position: absolute;
  left: 50%;
  top: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}

.main-content {
  overflow: auto;
  height: 100%;
  scroll-behavior: auto;
}
.media {
  margin: 15px;
  color: #fff !important;
  border: 1px solid #fff;
}
.media img {
  width: 150px;
  height: 150px;
}
.media-body {
  padding-left: 25px !important;
}
.media-body h5 {
  color: black;
  font-size: 40px;
  font-weight: 600;
  width: 100%;
  text-transform: uppercase;
}
.media-body p {
  padding-left: 20px;
  font-size: 1.2em;
  margin-bottom: 0.5em;
  color: rgb(104, 103, 103);
  font-family: Cambria, Cochin, Georgia, Times, "Times New Roman", serif;
}
.mr-3 {
  margin-top: 25px;
  width: 150px !important;
  height: 150px !important;
}
.mr-3 {
  border: 1px solid grey;
  border-radius: 50%;
}

section {
  border-bottom: 1px solid grey;
  padding: 2%;
}
.display-grades-list {
  color: #000;
  margin-left: 25px;
}
.display-grades-list p {
  margin-bottom: 0px;
  font-size: 18px;
}

.section-tag {
  margin-top: 25px;
}

.tag-output {
  padding: 25px;
}
.tag-output span {
  padding-right: 10px;
  height: 35px;
}
.tag-output button {
  margin-bottom: 10px;
}

.tag-output input {
  margin-top: 10px;
  width: 200px;
}

